CA,eAAA;EACA,kBAAA;EACA,wCAAA;EACA,aAAA;EACA,mBAAA;EACA,WAAA;CACA;AACA;EACA,aAAA;CACA;AACA;EACA,cAAA;EACA,mBAAA;EACA,aAAA;EACA,YAAA;EACA,aAAA;EACA,0BAAA;;EAEA,cAAA;EACA,sBAAA;EACA,eAAA;EACA,kBAAA;EACA,wCAAA;EACA,aAAA;EACA,mBAAA;EACA,WAAA;CACA;AACA;EACA,0BAAA;CACA;AACA;EACA,cAAA;EACA,mBAAA;CACA","file":"postorder.vue","sourcesContent":["<template>\n  <div class=\"wrapper\">\n      <scroller class=\"scroller\">\n      <text class='pay-tip tip-head'>订单信息</text>\n      <div class=\"row\">\n        <div class=\"order-order\">\n          <div class='v-h'></div>\n          <text class=\"button-order-text\">{{num}}桶{{typeStr}} 总价{{total}}元</text>\n          <div class='v-h'></div>\n        </div>\n      </div>\n\n      <text class='pay-tip tip-main'>选择桶装水类型</text>\n      <div class=\"row\">\n        <div class=\"order-info\" @click=\"pickType\">\n          <div class='v-h'></div>\n          <text class=\"button-text\">点击选择：{{typeStr}}</text>\n          <div class='v-h'></div>\n        </div>\n      </div>\n        <text class='pay-tip'>选择订购数量</text>\n        <div class=\"row\">\n          <div class=\"order-num\">\n            <div class='v-h'></div>\n            <input @input=\"onNumChange\" ref='num' class=\"nums\" type=\"number\" placeholder=\"输入数量，默一桶\" value=\"\"/>\n            <div class='v-h'></div>\n          </div>\n        </div>\n\n\n        <text class='pay-tip tip-pay'>选择支付方式</text>\n          <div class=\"row\">\n            <div class=\"pay-item\">\n              <div class='v-h'></div>\n              <div class=\"h-container\">\n                <image style=\"width: 88px;height: 88px;\" :src=\"getImage('alipay.png')\"></image>\n                <div class='pay-text' style=\"height: 88px;\">\n                  <div class='v-h v-h-top'></div>\n                  <text class='pay-way'>支付宝</text>\n                  <div class='v-h'></div>\n                  <text class='pay-way tip'>亿万用户都在用，安全可托付</text>\n                  <div class='v-h v-h-bottom'></div>\n                </div>\n                <div style=\"height: 88px;\">\n                  <div class='v-h'></div>\n                  <image style=\"width: 48px;height: 48px;\" :src=\"isAliPayUrl\"  @click='changePayWay(0)'></image>\n                  <div class='v-h'></div>\n                </div>\n              </div>\n              <div class='v-h'></div>\n            </div>\n          </div>\n          <div class=\"row\">\n            <div class=\"pay-item\">\n              <div class='v-h'></div>\n              <div class=\"h-container\">\n                <image style=\"width: 88px;height: 88px;\" :src=\"getImage('wxpay.png')\"></image>\n                <div class='pay-text' style=\"height: 88px;\">\n                  <div class='v-h v-h-top'></div>\n                  <text class='pay-way'>微信支付</text>\n                  <div class='v-h'></div>\n                  <text class='pay-way tip'>亿万用户的选择，安全快捷</text>\n                  <div class='v-h v-h-bottom'></div>\n                </div>\n                <div style=\"height: 88px;\">\n                  <div class='v-h'></div>\n                  <image style=\"width: 48px;height: 48px;\" :src=\"isWxPayUrl\"  @click='changePayWay(1)'></image>\n                  <div class='v-h'></div>\n              </div>\n              </div>\n              <div class='v-h'></div>\n            </div>\n          </div>\n        </scroller>\n\n      <div :class=\"[nowSubBack]\" @click=\"pay\">\n        <div class='v-h'></div>\n        <text class=\"sb-txt\" @click=\"pay\">确认支付  ￥{{total}}元</text>\n        <div class='v-h'></div>\n      </div>\n  </div>\n</template>\n\n<script>\n  let modal = weex.requireModule('modal')\n  const picker = weex.requireModule('picker')\n  let navigator = weex.requireModule('navigator')\n  let UrlTools = require('./UrlTools.js')\n  let price = [2000, 1000, 500]\n  let UnionPay = weex.requireModule('unionPay')\n  var stream = weex.requireModule('stream')\n  var globalEvent = weex.requireModule('globalEvent');\n  export default {\n    data: function () {\n      return {\n        total: 10,\n        num: 1,\n        index: 1,\n        typeStr: '中桶水',\n        types: ['大桶水', '中桶水', '小桶水'],\n        isAliPay: true,\n        isAliPayUrl:'',\n        isWxPayUrl:'',\n\n        nowSubBack: 'sbAli'\n      }\n    },\n    methods: {\n      pay: function() {\n        let platform = this.isAliPay ? 'ali':'wx'\n        if(UnionPay) {\n          UnionPay.pay({\n            platform: platform,\n            type: this.index,\n            num: this.num\n          }, result => {\n            if (this.isAliPay) {\n              if(result && result.resultStatus === '9000') {\n                modal.toast({\n                  message:'支付成功',\n                  duration: 1\n                })\n                setItem(_=>{\n                  navigator.pop()\n                }, 500)\n              } else {\n                modal.toast({\n                  message:'支付失败',\n                  duration: 1\n                })\n              }\n            } else {\n              modal.toast({\n                message:'微信支付还未实现',\n                duration: 1\n              })\n\n            }\n            /*\n            if(result === 'success') {\n              modal.toast({\n                message:'支付成功',\n                duration: 0.3\n              })\n              navigator.pop()\n            } else {\n              modal.toast({\n                message:'支付失败',\n                duration: 0.3\n              })\n            }\n            */\n          })\n        } else {\n          modal.toast({\n            message:'当前环境不支持支付操作'\n          })\n        }\n      },\n      getStarCount (repo, callback) {\n        return stream.fetch({\n          method: 'GET',\n          type: 'json',\n          url: 'https://aaa.bigfacewo.com/dwssserver/prepay/ali/jsen/0/1'\n        }, callback)\n      },\n      pickType: function() {\n        picker.pick({\n          index: 1,\n          items: this.types\n        }, event => {\n          if (event.result === 'success') {\n            this.index = event.data\n            this.typeStr = this.types[this.index]\n            this.total = this.calPrice()\n          }\n        })\n      },\n      calPrice: function() {\n        let p = parseFloat(price[this.index] * this.num) / 100.0\n        return p\n      },\n      onNumChange: function(e) {\n        if(this.checkNum(e.value)) {\n          this.num = parseInt(e.value)\n        } else {\n          this.$refs.num.value('1')\n        }\n        this.total = this.calPrice()\n      },\n      checkNum: function (txt) {\n    　　var re = /^[0-9]+.?[0-9]*$/; //判断字符串是否为数字 //判断正整数 /^[1-9]+[0-9]*]*$/\n\n    　　if (!re.test(txt)) {\n  　　　　return false\n    　　}\n        return true\n      },\n      getImage: function(imageName) {\n        return UrlTools.getImageBase(weex.config.bundleUrl) + '/dist/' + imageName\n      },\n      changePayWay: function(index) {\n        if(index === 0) {\n          this.nowSubBack = 'sbAli'\n          this.isAliPay = true\n          this.isAliPayUrl = UrlTools.getImageBase(weex.config.bundleUrl) + '/dist/checked.png'\n          this.isWxPayUrl = UrlTools.getImageBase(weex.config.bundleUrl) + '/dist/check.png'\n        } else {\n          this.nowSubBack = 'sbWx'\n          this.isAliPay = false\n          this.isAliPayUrl = UrlTools.getImageBase(weex.config.bundleUrl) + '/dist/check.png'\n          this.isWxPayUrl = UrlTools.getImageBase(weex.config.bundleUrl) + '/dist/checked.png'\n        }\n      }\n    },\n    mounted: function() {\n      globalEvent.addEventListener(\"PayResult\", result => {\n        console.log(result)\n      });\n\n      if(weex.config.env.platform !== 'iOS') {\n        navigator.setNavBarTitle('订单支付')\n        // navigator.clearNavBarLeftItem()\n      } else {\n        navigator.setNavBarTitle({title:'订单支付'})\n      }\n      let params = UrlTools.getParams(weex.config.bundleUrl)\n      if(params.type) {\n        let type = parseInt(params.type)\n        this.index = type\n        this.typeStr = this.types[this.index]\n        this.total = this.calPrice()\n      }\n      this.changePayWay(0)\n    }\n  }\n</script>\n\n<style>\n\n  .scroller {\n      position: absolute;\n      top:0px;\n      bottom:110px;\n      left: 0px;\n      right:0px;\n  }\n  .wrapper {\n    position: absolute;\n    background-color: #eeeeee;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n  }\n  .order-order {\n    flex: 1;\n    flex-direction: column;\n    width: 100%;\n    right: 0px;\n    background-color: #ffffff;\n    height:100px;\n  }\n  .order-info{\n    flex: 1;\n    flex-direction: column;\n    width: 100%;\n    right: 0px;\n    background-color: #ffffff;\n    height:100px;\n  }\n  .order-num{\n    flex: 1;\n    flex-direction: column;\n    width: 100%;\n    right: 0px;\n    background-color: #ffffff;\n    height:100px;\n  }\n  .nums {\n    padding-left: 60px;\n    padding-right: 60px;\n    color: #000000;\n    placeholder-color: #bbbbbb;\n  }\n  .pay-item {\n    height:120px;\n    flex: 1;\n    width: 100%;\n    right: 0px;\n    padding-left: 40px;\n    padding-right: 40px;\n    background-color: #ffffff;\n    margin-bottom: 2px;\n  }\n  .h-container {\n    flex-direction: row;\n  }\n  .pay-text {\n    flex: 1;\n    padding-left: 20px;\n  }\n  .pay-way {\n  }\n  .pay-tip {\n    padding-left: 20px;\n    padding-top: 20px;\n    padding-bottom: 8px;\n    font-size: 28px;\n    color: #888888;\n  }\n  .tip-main {\n    margin-top: 40px;\n  }\n  .tip-head {\n    margin-top: 0px;\n  }\n  .tip-pay {\n    margin-top: 40px;\n  }\n  .tip {\n    font-size: 22px;\n  }\n  .v-h-top {\n    flex: 0.2;\n  }\n  .v-h-bottom {\n    flex: 0.3;\n  }\n  .row{\n    flex-direction: row;\n  }\n  .v-h {\n    flex:1;\n  }\n  .button-text{\n    padding-left: 60px;\n    padding-right: 60px;\n    color: #000000;\n    font-size: 35px;\n  }\n  .button-order-text{\n    padding-left: 60px;\n    padding-right: 60px;\n    color: #000000;\n    font-size: 42px;\n  }\n  .submit-btn {\n    position: absolute;\n    bottom: 10px;\n    left: 120px;\n    right: 120px;\n    background-color: #2595c9;\n\n    display:block;\n    box-sizing:border-box;\n    font-size:18px;\n    border-radius:5px;\n    -webkit-tap-highlight-color:transparent;\n    height: 90px;\n    text-align: center;\n  }\n  .sbAli {\n    z-index: 1000;\n    position: absolute;\n    bottom: 10px;\n    left: 120px;\n    right: 120px;\n    background-color: #2595c9;\n\n    display:block;\n    box-sizing:border-box;\n    font-size:18px;\n    border-radius:5px;\n    -webkit-tap-highlight-color:transparent;\n    height: 90px;\n    text-align: center;\n    opacity: 1;\n  }\n  .sbAli:active {\n    opacity: 0.8;\n  }\n  .sbWx {\n    z-index: 1000;\n    position: absolute;\n    bottom: 10px;\n    left: 120px;\n    right: 120px;\n    background-color: #48a443;\n\n    display:block;\n    box-sizing:border-box;\n    font-size:18px;\n    border-radius:5px;\n    -webkit-tap-highlight-color:transparent;\n    height: 90px;\n    text-align: center;\n    opacity: 1;\n  }\n  .bt-submit:active {\n    background-color: #0A9D09;\n  }\n  .sb-txt {\n    color:#FFFFFF;\n    text-align: center;\n  }\n</style>\n"],"sourceRoot":""}]);

